import "./App.css";
import { BrowserRouter, Redirect, Route,Switch } from "react-router-dom";
import React, {lazy, Suspense} from 'react'
const UsersLayout = lazy(()=>import("./UsersLayout/UsersLayout"));
const Navigation = lazy(() => import("./NavigationLayout/NavigationLayout"))
const SuperHeroLayout = lazy(() => import("./SuperHeroLayout/SuperHeroLayout")) 
const ChartsLayout = lazy(() => import("./ChartsLayout/ChartsLayout"))

function App() {
  return (
    <BrowserRouter>
        <Suspense fallback={<div>Loading .. . . .</div>}>
      <div className="App">
        <Navigation />
        <Switch>
        <Route path="/" exact render={() => <Redirect to="/charts"/>} />
        <Route path="/charts" exact component={ChartsLayout} />
        <Route path="/super-hero" exact component={SuperHeroLayout} />
        <Route path="/users" exact component={UsersLayout} />
        </Switch>
      </div>
        </Suspense>
    </BrowserRouter>
  );
}

export default App;
